"use client";
import { useState, useRef } from "react";
import { motion } from "framer-motion";
import { UploadCloud, PencilLine, FileText } from "lucide-react";
import axios, { AxiosError } from "axios";

interface ApiErrorResponse {
  message?: string;
  // Add other expected error response properties here if needed
}

interface UploadResponse {
  // Define the expected shape of your successful response here
  success: boolean;
  message?: string;
  filePath?: string;
  // Add other expected response properties
}

export default function ResumeUploader() {
  const [file, setFile] = useState<File | null>(null);
  const [isEditing, setIsEditing] = useState(false);
  const fileInputRef = useRef<HTMLInputElement | null>(null);

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const selectedFile = e.target.files?.[0];
    if (selectedFile && selectedFile.type === "application/pdf") {
      setFile(selectedFile);
      setIsEditing(false);
    } else {
      alert("Please upload a valid PDF file.");
    }
  };

  const handleUpload = async () => {
    if (!file) return;

    const formData = new FormData();
    formData.append("resume", file);

    try {
      const res = await axios.post<UploadResponse>(
        `${process.env.API_URL}resume/upload`,
        formData,
        {
          headers: { "Content-Type": "multipart/form-data" },
          withCredentials: true,
        }
      );

      console.log("✅ Upload success:", res.data);
      alert("Resume uploaded successfully!");
    } catch (error) {
      const axiosError = error as AxiosError<ApiErrorResponse>;
      console.error("❌ Upload failed:", axiosError);
      alert(axiosError.response?.data?.message || "Upload failed");
    }
  };

  const triggerFileSelect = () => {
    fileInputRef.current?.click();
  };

  return (
    <motion.div
      className="p-6 rounded-xl shadow-md space-y-4"
      initial={{ opacity: 0, x: -50 }}
      animate={{ opacity: 1, x: 0 }}
    >
      <h2 className="text-xl font-semibold flex items-center gap-2">
        <UploadCloud className="w-5 h-5" /> Upload / Edit Resume
      </h2>

      {!file && (
        <>
          <input
            type="file"
            accept="application/pdf"
            onChange={handleFileChange}
            ref={fileInputRef}
            className="hidden"
          />
          <button
            onClick={triggerFileSelect}
            className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded-md"
          >
            Upload Resume
          </button>
        </>
      )}

      {file && (
        <div className="space-y-2">
          <div className="flex items-center justify-between px-4 py-2 rounded-md">
            <span className="flex items-center gap-2 text-sm">
              <FileText className="w-4 h-4" />
              {file.name}
            </span>
            <button
              onClick={() => setIsEditing(true)}
              className="text-blue-600 hover:underline text-sm flex items-center gap-1"
            >
              <PencilLine className="w-4 h-4" /> Edit
            </button>
          </div>

          {isEditing && (
            <>
              <input
                type="file"
                accept="application/pdf"
                onChange={handleFileChange}
                ref={fileInputRef}
                className="hidden"
              />
              <button
                onClick={triggerFileSelect}
                className="bg-yellow-500 hover:bg-yellow-600 text-white px-4 py-2 rounded-md"
              >
                Choose New Resume
              </button>
            </>
          )}

          <button
            onClick={handleUpload}
            className="bg-green-600 hover:bg-green-700 text-white px-4 py-2 rounded-md w-full"
          >
            Upload Now
          </button>
        </div>
      )}
    </motion.div>
  );
}